$breakpoint-xs: 599px;

.button {
    position: relative;
    margin-bottom: auto;
    margin-top: 10px;
    transition: all 0.2s ease-in-out;
    &.b2 {
        width: 120px;
    }
    &.b3 {
        height: 34px;
    }
    &.b4 {
        border: 1px solid #3037ee;
    }
    &.b5 {
        padding: 2px;
    }
    &.b6 {
        border-radius: 5px;
    }
    &.b7 {
        line-height: 34px;
    }
    &.b8 {
        cursor: pointer;
    }
    &.b9 {
        color:white;
    }
    &.b10 {    
        background-color: #4b51f2;
        box-shadow: 0px 3px 1px -2px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 1px 5px 0px rgba(0,0,0,0.12);
    }
    &.b12 {
        .innerButton {
            position: absolute;
        }
    }
    &.b13 {
        .innerButton {
            width:100%;
        }
    }
    &.b14 {
        .innerButton {
            height:100%;
        }
    }
    &.b15 {
        .innerButton {
            top: 0;
        }
    }
    &.b16 {
        .innerButton {
            left: 0;
        }
    }
    &.b17 {
        .innerButton {
            transition: all 0.3s ease-in-out;
        }
    }
    &.b18 {
        .innerButton {
            clip-path: circle(0% at 50% 50%);
        }
    }
    &.b21 {
        &:hover .innerButton {
            clip-path: circle(75%);
        }
    }
    &.b22 {
        &:hover .innerButton {
            background-color: rgba(0,0,0,0.3);
        }
    }

}
