@import "node_modules/mathsass/dist/math";

.hamburger_1{
  transform:translate3d(0,-8px,0);
}
.hamburger_2{
  transform:translate3d(0,0,0);
}
.hamburger_3{
  transform:translate3d(0,8px,0);
}
.menu_open:checked+.menu_open_button{
  .hamburger_1{
    transform:translate3d(0,0,0) rotate(45deg); 
  }
  .hamburger_2{
    transform:translate3d(0,0,0) scale(0.1,1);
  }
  .hamburger_3{
    transform:translate3d(0,0,0) rotate(-45deg); 
  }
}
.hamburger1,.hamburger2,.hamburger3 {
    // transition-timing-function:cubic-bezier(0.175, 0.885, 0.320, 1.275);
    transition-duration:400ms;
}
.hamburgerRotate1 {
    transform:translate3d(0,100vh*0.013,0);
}
.hamburgerRotate3 {
    transform:translate3d(0,-100vh*0.013,0);
}
.MainMenu {
    width:100%;
    height:100%;
}
.hexSvgBtnContainer {
    width:100%;
    height:100%;
    position: relative;
    display:flex;
    // display:flex;
}
.hexSvgBtn {
    z-index:2;
    transition-timing-function:cubic-bezier(0.175, 0.885, 0.320, 1.275);
    transition-duration:400ms;
    border-radius: 25%!important;
    margin: auto auto;
}
.hexSvg {
    z-index:6;
}
.hexSvg0Path {
    z-index:5;
}
.hexSvg0Shadow {
    z-index:4;
    fill: rgba(0,0,0,0.3);
}
.hexSvgPathHovering {
    fill: #fff;
}
.hexSvgPathIconHovering {
    fill: #0e153a;
}
.hexSvgReduce {
    // transition-timing-function:cubic-bezier(0.175, 0.885, 0.320, 1.275);
    // transition-duration:400ms;
    height:100vh*0.2!important;
    width:100vw*0.2!important;
    margin-left: -(100vw*0.2)*0.5!important;
    margin-top: -(100vh*0.2)*0.5!important;
}
.hexSvg,.hexSvg1,.hexSvg2,.hexSvg3,.hexSvg4,.hexSvg5 {
    position: absolute;
    cursor: pointer;
    height:70vh*0.4;
    width:70vw*0.4;
    left: 50%;
    margin-left: -(70vw*0.4)*0.5;
    top: 50%;
    margin-top: -(70vh*0.4)*0.5;
    transition-timing-function:cubic-bezier(0.175, 0.885, 0.320, 1.275);
    transition-duration:400ms;
}
.hexSvg1,.hexSvg2,.hexSvg3,.hexSvg4,.hexSvg5 {
    position: absolute;
}
.hexSvg1Move {
    transform:translate3d(120vh*0.2,-45vh*0.2,0)
}
.hexSvg2Move {
    transform:translate3d(0vh*0.2,-140vh*0.2,0)
}
.hexSvg3Move {
    transform:translate3d(80vh*0.2,100vh*0.2,0)
}
.hexSvg4Move {
    transform:translate3d(-80vh*0.2,100vh*0.2,0)
}
.hexSvg5Move {
    transform:translate3d(-120vh*0.2,-45vh*0.2,0)
}
.urlIcon,
.urlIcon {
    width: 100px!important;
    height: 100px!important;
    background-size: 20px 30px;
    color:red;
}
